.contact-page {
  background-color: #002ea6;
}
.contact-page strong {
  color: #17fcc4;
}
@media (min-width: 1025px) {
  .contact-page .contacts-hero h3,
  .contact-page .contacts-hero .h3 {
    margin-bottom: 30px;
  }
}
@media (min-width: 1441px) {
  .contact-page .contacts-hero h3,
  .contact-page .contacts-hero .h3 {
    margin-bottom: 40px;
  }
}
.contact-page .txt-content {
  width: 100%;
  max-width: 525px;
}
@media (max-width: 1024px) {
  .contact-page .txt-content {
    padding-right: 40px;
  }
}
@media (min-width: 1025px) {
  .contact-page .txt-content {
    max-width: 27.344vw;
  }
}
@media (min-width: 1921px) {
  .contact-page .txt-content {
    max-width: 525px;
  }
}
.contact-page .team-images {
  right: 0;
  top: 50px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  flex-wrap: wrap;
}
@media (max-width: 1024px) {
  .contact-page .team-images {
    display: none;
  }
}
@media (min-width: 1025px) {
  .contact-page .team-images {
    width: 39.063vw;
  }
}
@media (min-width: 1441px) {
  .contact-page .team-images {
    width: 52.083vw;
  }
}
@media (min-width: 1921px) {
  .contact-page .team-images {
    width: 1000px;
  }
}
.contact-page .team-images .single-team-member {
  display: inline-block;
}
.contact-page .team-images .single-team-member img {
  border-radius: 50%;
}
@media (min-width: 1025px) {
  .contact-page .team-images .single-team-member {
    width: 7.708vw;
    height: 7.708vw;
  }
}
@media (min-width: 1441px) {
  .contact-page .team-images .single-team-member {
    width: 10.313vw;
    height: 10.313vw;
  }
}
@media (min-width: 1921px) {
  .contact-page .team-images .single-team-member {
    width: 200px;
    height: 200px;
  }
}
.contact-page .mobile-team-images {
  right: 0;
  top: 50px;
  width: 140px;
}
@media (min-width: 1025px) {
  .contact-page .mobile-team-images {
    display: none;
  }
}
.contact-page .mobile-team-images .single-team-member {
  display: inline-block;
  width: 70px;
  height: 70px;
}
.contact-page .mobile-team-images .single-team-member img {
  border-radius: 50%;
}
.contact-page .background-svg-wrap.svg-contact-hero {
  left: 0;
  top: 30px;
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
}
@media (min-width: 1025px) {
  .contact-page .background-svg-wrap.svg-contact-hero {
    width: 65.625vw;
    height: 65.625vw;
  }
}
@media (min-width: 1921px) {
  .contact-page .background-svg-wrap.svg-contact-hero {
    width: 1260px;
    height: 1260px;
  }
}
@media (min-width: 1025px) {
  .contact-page .background-svg-wrap.svg-contact-hero .background-flower-path-svg {
    width: 32.813vw;
    height: 32.813vw;
  }
}
@media (min-width: 1921px) {
  .contact-page .background-svg-wrap.svg-contact-hero .background-flower-path-svg {
    width: 630px;
    height: 630px;
  }
}
.contact-page .background-svg-wrap.svg-contact-hero .st0 {
  fill: none;
  stroke-miterlimit: 10;
}
.contact-page .background-svg-wrap.svg-contact-hero .svg-full {
  opacity: 0.1;
}
.contact-page .background-svg-wrap.svg-contact-hero .svg-full .st0 {
  fill: #7f96d2;
}
.contact-page .background-svg-wrap.svg-contact-hero .svg-stroke {
  opacity: 0.2;
}
.contact-page .background-svg-wrap.svg-contact-hero .svg-stroke .st0 {
  stroke: #7f96d2;
}
.contact-page .background-svg-wrap.svg-contact-hero .svg-stroke .st1 {
  fill: #7f96d2;
}
@media (max-width: 1024px) {
  .contact-page .background-svg-wrap.svg-contact-hero {
    display: none;
  }
}
.contact-tabs {
  z-index: 9;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1025px) {
  .contact-tabs {
    flex-direction: row;
  }
}
.contact-tabs .container {
  display: flex;
}
@media (max-width: 1024px) {
  .contact-tabs .container {
    flex-direction: column;
  }
}
.contact-tabs .container .testimonial-content-tab {
  z-index: 2;
}
@media (max-width: 1024px) {
  .contact-tabs .container .testimonial-content-tab {
    order: 1;
  }
}
@media (min-width: 1025px) {
  .contact-tabs .container .testimonial-content-tab {
    position: absolute;
    left: 0;
    top: 200px;
  }
}
.contact-tabs .container .testimonial-content-tab img {
  max-width: 100px;
}
.contact-tabs .tabs-nav {
  z-index: 3;
}
@media (max-width: 1024px) {
  .contact-tabs .tabs-nav {
    margin-bottom: 40px;
  }
}
.contact-tabs .tabs-nav li {
  font-weight: 700;
  background-color: #fff;
  border-bottom: 1px solid #002ea6;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 1025px) {
  .contact-tabs .tabs-nav li {
    width: 350px;
  }
}
@media (min-width: 1441px) {
  .contact-tabs .tabs-nav li {
    width: 425px;
  }
}
.contact-tabs .tabs-nav li:hover, .contact-tabs .tabs-nav li:focus {
  cursor: pointer;
}
.contact-tabs .tabs-nav li a {
  width: 100%;
  padding: 20px 0 20px 20px;
  background-color: #17fcc4;
  transition: background-color 0.3s ease;
}
@media (min-width: 1025px) {
  .contact-tabs .tabs-nav li a {
    padding: 20px 0 20px 30px;
    padding-right: 40px;
  }
}
.contact-tabs .tabs-nav li a.inactive {
  background-color: transparent;
  transition: background-color 0.3s ease;
}
.contact-tabs .tabs-nav li a span {
  float: right;
  padding-right: 20px;
  transition: padding-right 0.3s ease;
}
.contact-tabs .tabs-nav li a:hover span, .contact-tabs .tabs-nav li a:focus span {
  padding-right: 10px;
  transition: padding-right 0.3s ease;
}
.contact-tabs .user-testimonial {
  display: flex;
  background-color: #f2f4fa;
  padding: 25px;
  margin-top: 70px;
}
@media (min-width: 1025px) {
  .contact-tabs .user-testimonial {
    width: 350px;
  }
}
@media (min-width: 1441px) {
  .contact-tabs .user-testimonial {
    width: 425px;
  }
}
.contact-tabs .user-testimonial .user-img {
  flex: 0 0 auto;
  margin-right: 25px;
}
@media (max-width: 767px) {
  .contact-tabs .user-testimonial .user-img {
    width: 60px;
  }
}
.contact-tabs .user-testimonial .testimonial-content img {
  height: 24px;
  margin-bottom: 15px;
}
.contact-tabs .user-testimonial .testimonial-content .txt-size-18 {
  margin-bottom: 25px;
}
@media (min-width: 1025px) {
  .contact-tabs .tab-content {
    margin-left: 5.208vw;
  }
}
.contact-tabs .tab-content .tab-content-heading {
  font-weight: 700;
}
.contact-tabs .tab-content .tab-subheading {
  font-weight: 700;
  margin: 25px 0px;
}
@media (min-width: 1025px) {
  .contact-tabs .tab-content .tab-subheading {
    margin-top: 2.604vw;
    margin-bottom: 2.604vw;
  }
}
@media (min-width: 1025px) {
  .contact-tabs .tab-contacts {
    margin-bottom: 40px;
  }
}
.contact-tabs .single-tab-contact {
  margin-bottom: 15px;
  display: flex;
  align-items: center;
}
.contact-tabs .single-tab-contact img {
  margin-right: 30px;
}
.contact-tabs .contacts-tab-form .form-styles .form-label {
  background-color: #fff;
  color: #002ea6;
}
.contact-tabs .contacts-tab-form .form-styles .form-input {
  color: #002ea6;
}